.index{
    height: 85vh;
}
.bg{
    width: 30rem;
    animation: shake 3s infinite;
}
.who-i-am{
    animation: shake 5s infinite;
}
.bg-profile{
    width: 25rem;
    border-radius: 10px;
}

.index-title{
    font-size: 3.25rem;
    margin-top: 4rem;
}
.container-detail{
    margin: 0 10rem;
}
.profile-detail{
    padding: 0 6rem;
    justify-content: space-evenly!important;
}
@keyframes shake {
    10%{
        transform: rotate(10deg);
    }
    20%{
        transform: rotate(-10deg);
    }
    30%{
        transform: rotate(10deg);
    }
    50%{
        transform : rotate(0deg)
    }
}

@media screen and (max-width: 768px){
    .container{
        flex-direction: column!important;
    }
    .bg{
        width: 15rem;
        margin-top: 2rem;
    }
    .index-title{
        font-size: 1.5rem;
        margin-top: 1rem!important;
    }
    .animation{
        font-size: 1rem;
    }
    .container-detail{
        flex-direction: column!important;
        margin: 0 1rem!important;
    }
    .bg-profile{
        width: 12rem;
    }
    .profile-detail{
        padding: 0 1rem!important;
    }
    
    

}