.footer{
    height: 20rem;
    width: 100%;
    border-top:2px solid #000;
    z-index: 100;
}
.icon-fa{
    font-size: 5rem;
    margin: 3rem 4rem;
    color: #eee;
}

.icon-fa:hover{
    cursor: pointer;
    color: #0070E1;

}
@keyframes hover{
    0%{
        font-size: 7rem;
    }
    25%{
        font-size: 6rem;
    }
    50%{
        font-size: 8rem;
    }
    75%{
        font-size: 6rem
    }
    100%{
        font-size: 5rem;        
    }
}
@media screen and (max-width: 768px){
    .icon-fa {
        font-size: 3rem;
        margin: 1rem 0.5rem;
    }
}