*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #0A192F;
}

.flex{
  display: flex;
  align-items: center;
  justify-content: center;
}
.row{
  flex-direction: row;
}
.column{
  flex-direction: column;
}
.color1{
  color: #707070;
}
.color2{
  color: #0070E1;
}
.color3{
  color: #393939
}
.color-eee{
  color: #eee;
}
.pt-1{
  padding-top: 1rem;
}
.pt-2{
  padding-top: 2rem;
}
.pad-r-l{
  padding: 0 1rem;
}

.navbar{
  position: fixed;
  top: 0;
  display: flex;
  width: 100%;
  height: 3rem;
  align-items: center;
  justify-content: space-around;
  border-bottom: #0070E1;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.708);
  z-index: 100;
}
.navbar-logo{
  color: #eee;
}
.navbarItems{
  display: inline-block;
  background-color: #fff;
}
.navbarItem{
  color: #eee;
  padding: 0 1rem;
  text-decoration: none;
  transition: .3s;
  font-weight: 500;
  font-size: 1rem;
}
.navbarItem:hover{
  color: #0070E1;
}
.faLessIcon{
  color: #0A192F;
}
.navbarItem:hover .faLessIcon{
  color: #0070E1;
}
span{
  color: #0070E1;
}
.text-center{
  text-align: justify;
  line-height: 1.5rem;
  font-size: 1rem;
}
.btnBars{
  display: none;
}
.less-invert{
  transform: rotate(180deg);
}
.animation{
  color: #eee;
  display: block;
  white-space: nowrap;
  border-right: 4px solid;
  width: 22ch;
  animation: typing 5s steps(24) infinite alternate,
  blink .5s infinite step-end alternate;
  overflow: hidden;
}
@keyframes typing {
  from {
    width: 0;
  }
  
}
@keyframes blink {
  50% {border-color: transparent;}
}
.animation2{
  display: block;
  white-space: nowrap;
  border-right: 4px solid;
  width: 19ch;
  animation: typing 5s steps(19) infinite alternate,
  blink .5s infinite step-end alternate;
  overflow: hidden;
  animation-delay: 10s;
}


@media screen and (max-width: 768px){
  .btnBars{
    display: inline-block;
    position: absolute;
    font-size: 2rem;
    right: 0;
  }
  .navbarItems{
    display: none;
  }
  .navbarItems.active{
    display: flex;
    position: absolute;
    flex-direction: column;
    top: 5rem;
    height: 85vh;
    width: 100%;
    background: red;
    align-items: center;
    justify-content: center;
  }
  .navbarItem{
    color: #fff;
    text-decoration: none;
  }

}
