.knowlegde-container{
    margin: 0 7rem;
    flex-wrap: wrap;
}
.conocimientos-items{
    margin: .3rem .2rem;
    width: 25%;
    height: 15rem;
    border-radius: 10px;
    cursor: pointer;
}
.conocimientos-title{
    font-size: 2rem;
    color: #fff;
}
.conocimientos-icon{
    font-size: 6rem;
    color: #eee;
}
@keyframes move {
    0%{
        font-size: 6rem;
    }
    25%{
        font-size: 8rem;
    }
    50%{
        font-size: 5rem;
    }
    75%{
        font-size: 7rem;
    }
    100%{
        font-size: 6rem;
    }
}
.Css:hover{
    color: #146EB0;
    animation: move 1s ;
}
.Js:hover{
    color: yellow;
    background-color: #000;
    animation: move 1s ;
}
.React{
    animation: rotate 6s infinite;
}
.React:hover{
    border-radius: 50%;
    color: #5ED3F3;
    animation: move 1s, rotate 6s infinite ;
}
.Mui:hover{
    color: #0079F2;
    animation: move 1s ;
}
.Git:hover{
    color: #E74B27;
    animation: move 1s ;
}
.Bootstrap:hover{
    color: #583979;
    animation: move 1s ;
}
.Jq:hover{
    color: #0863A2;
    animation: move 1s ;
}
.Sass:hover{
    color: #CE508E;
    animation: move 1s ;
}

@keyframes rotate{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

@media screen and (max-width: 768px){
    .knowlegde-container{
        margin: 0 1rem;
    }
    .conocimientos-items{
        width: 100%;
    }
    .Css{
        color: #146EB0;
    }
    .Js{
        color: yellow;
    }
    .React{
        animation: rotate 6s infinite;
        border-radius: 50%;
        color: #5ED3F3;
    }
    .Mui{
        color: #0079F2;
    }
    .Git{
        color: #E74B27;
    }
    .Bootstrap{
        color: #583979;
    }
    .Jq{
        color: #0863A2;
    }
    .Sass{
        color: #CE508E;
    }
}