.project-section{
    background-color:beige;
    margin-top: 4rem;
}
.project-container{
    flex-wrap: wrap;
    width: 100%;
    padding: 1 4rem;
    gap: 10;
}

.project-div{
    margin: .5rem;
    width: 30rem;
    padding: 0 4rem;
    border-radius: 10px;
}

.project-image{
    width: 30rem;
    border-radius: 20px;
}
.project-image:hover{
    cursor: pointer;
}
.link-icon{
    font-size: 2rem;
    margin: 0 1rem;
    color: #fff;
}
.link-icon:hover{
    color: #6FD1A3;
}
.tool{
    color: #6FD1A3;
    font-weight: bold;
    font-size: 1.2rem;
}
@media screen and (max-width: 768px){
    .project-div{
        padding: 0;
        margin: .5rem auto;
    }
    .project-title{
        font-size: 1.2rem;
    }
    .project-subtitle{
        font-size: 1rem;
        padding: 1rem 2rem;
        font-size: 1rem;
        text-align: justify;
    }
    .project-image{
        width: 19rem;
    }
    .pt-1{
        padding: 1rem 2rem!important;
    }
}